import { useState } from "react";
import { Phone } from "../types/Phone";

export function useLocalStorage(key: string, initialValue: Phone[]) {
  const [storedValue, setStoredValue] = useState<Phone[]>(() => {
    const item = localStorage.getItem(key);

    return item ? JSON.parse(item) : initialValue;
  });

  const setValue = (value: Phone) => {
    const existingItems = localStorage.getItem(key);

    const collection: Phone[] = existingItems
      ? JSON.parse(existingItems)
      : [];

    const isItemInStorage = collection.find(item => (
      item.id === value.id
    ));

    if (!isItemInStorage) {
      collection.push(value);
    }

    setStoredValue(collection);

    localStorage.setItem(key, JSON.stringify(collection));
  };

  // const removeValue = (value: Phone) => {
  //   const existingItems = localStorage.getItem(key);

  //   const collection: Phone[] = existingItems
  //     ? JSON.parse(existingItems)
  //     : [];

  //   const isItemInStorage = collection.find(item => (
  //     item.id === value.id
  //   ));

  //   if (!isItemInStorage) {
  //     collection.push(value);
  //   }

  //   setStoredValue(collection);

  //   localStorage.setItem(key, JSON.stringify(collection));
  // };

  return [storedValue, setValue] as const;
}
